export default {
  name: "Header",
  data() {
    return {
      menu: {
        userBanker: {
          name: 'userBanker',
          count: 0,
          label: '银商'
        }, // 银商
        bank: {
          name: 'bank',
          count: 0,
          label: '银行卡',
        }, // 银行卡
        thirdPay: {
          name: 'thirdPay',
          count: 0,
          label: '第三方',
        }, // 第三方
        withdraw: {
          name: 'withdraw',
          count: 0,
          label: '兑换',
        }  // 兑换
      }
    }
  },
  methods:{
    forward(name) {
      this.$emit('forward', {name})
    }
  },
  mounted() {

  }
}



